import './index.css'
import React from 'react'
import ReactDOM from 'react-dom'
import { MassPoints, RCMap, TileLayer } from '../src/components'

const center: [number, number] = [28.16112, 120.81274]

const points: [number, number][] = Array(1000).fill(undefined).map(() => [27.96112 + Math.random() / 5, 120.61274 + Math.random() / 2])

const massLayerPoints = points.map(position => ({ position }))

const App: React.FC = (): JSX.Element => (
  <RCMap center={center} zoom={2} bounds={points}>
    <TileLayer url='https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png' />
    <MassPoints points={massLayerPoints} />
  </RCMap>
)

ReactDOM.render(<App />, document.querySelector('#root'))
